.game-container {
    text-align: center;
    font-family: Arial, sans-serif;
    margin: 50px auto;
    width: 80%;
    max-width: 600px;
    background: linear-gradient(to right, #ff512f, #dd2476);
    color: white;
    padding: 20px;
    border-radius: 10px;
}

.question-card {
    background: rgba(255, 255, 255, 0.1);
    padding: 20px;
    border-radius: 10px;
}

button {
    background-color: #ff9800;
    border: none;
    color: white;
    padding: 10px 20px;
    margin: 5px;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
}

button:hover {
    background-color: #e65100;
}